import React, {useEffect} from "react";
import { stepData } from "../steps";
import { useApp } from "./AppContext";
import { Form } from "./Form";

function loop(n) { 
    for (var i in stepData) {
        if(stepData[i].step === n) {
            var retArray = [i, stepData[i].title, stepData[i].title]
            return retArray;
        }
    }  
}

export function CardText({phase}) {
    const {setCurrPhase, enablePhase, completePhase} = useApp();

    useEffect(() => {
        setCurrPhase(phase); 
    })

    useEffect(() => {
        enablePhase(phase);
        completePhase(phase); 
    }, [phase])
    
    return (
        <>
            <p>
                <b>{ stepData[loop(phase)[0]].title.charAt(0) }</b>
                { stepData[loop(phase)[0]].title.substring(1, stepData[loop(phase)[0]].length) }
            </p>
            <div className="card">
                <div className="card-body">
                    <h5 className="card-title"><b>{stepData[loop(phase)[0]].subtitle}</b></h5>
                    <p className="card-text">{stepData[loop(phase)[0]].description}</p>
                    <ol>
                        { phase==="E" && 
                            stepData[loop(phase)[0]].substeps.map((data) => (
                            <li key={data}>{data}</li>
                            ))
                        }
                        { phase==="F" && 
                            <Form />
                        }
                    </ol>
                </div>
            </div>
        </>
    );
}

export default CardText;